.button{
    padding:12px;
    font-size: 18px;
    font-weight: bold;
    text-decoration: none;
    text-align: center;
    white-space: nowrap;


    &--primary{
        color: #fff;
        background-color: #4f2ac3;
        
    }

    @media (max-width: 640px) {
        display: block;
            justify-content: center;
        width: 100%;
        
    }

    @media (max-width: 1024px) {
        display:flex;
        align-items: center;
    }
}