.hero{
    padding-top: 64px;
    padding-bottom: 100px;
    background-image: url(../img/hero.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    position: relative;

    .container{
        position: relative;  
    }

    &::before{
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: #000;
        content: '';
        opacity: .3;
    }

    &__text{
        padding-top: 64px;
        padding-bottom: 64px;
        font-size: 40px;
        line-height: 56px;
        font-weight: 800;
        text-align: center;
    }
    
    &__logo{
        display: block;
        margin: 0 auto;
        height: 80px;
    }

    @media (max-width: 640px) {
        padding: 40px 0;

        &__logo{
            height: 40px;

        }

        &__text{
            font-size: 22px;
            line-height: 24px;
            margin: 24px 0;
        }
    }
}